:root {
  --clr-dark: #202020;
  --clr-light: #eef2f6;
  --clr-accent: #62d3f5;

  --ff-openSans: 'Open Sans', sans-serif;

  --fs-logo: 10em;
  --fs-h1: 4.5rem;
  --fs-h2: 3.5rem;
  --fs-h3: 2.5rem;
  --fs-h4: 1.5rem;
  --fs-h5: 1.125rem;
  --fs-body: 1rem;
  --fs-small: 0.9rem;
}

@media (max-width: 1600px) {
  :root {
    --fs-logo: 8em;
    --fs-h1: 3rem;
    --fs-h2: 2rem;
    --fs-h3: 1.25rem;
    --fs-h4: 1rem;
    --fs-h5: 0.975rem;
    --fs-body: 0.875rem;
    --fs-small: 0.775rem;
  }
}

@media (max-width: 425px) {
  :root {
    --fs-logo: 6em;
    --fs-h1: 2rem;
    --fs-h2: 1.8rem;
    --fs-h3: 1rem;
    --fs-h4: 0.8rem;
    --fs-h5: 0.775rem;
    --fs-body: 0.675rem;
    --fs-small: 0.575rem;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  width: 90%;
  margin: 0 auto;
  padding: 0;
  line-height: 1.6;
  word-spacing: 1.4px;
  font-family: var(--ff-openSans);
  font-size: var(--fs-body);
  background-color: var(--clr-dark);
  color: var(--clr-light);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100vh;
}

button {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  display: inline-block;
  background: none;
  cursor: pointer;
  font-family: var(--ff-openSans);
  text-transform: capitalize;
}

a {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  display: inline-block;
  background: none;
  cursor: pointer;
  font-family: var(--ff-openSans);
  text-transform: capitalize;
  text-decoration: none;
  color: inherit;
}

img {
  pointer-events: none;
  user-select: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: var(--fs-h1);
  font-family: var(--ff-openSans);
}

h2 {
  font-size: var(--fs-h2);
  font-family: var(--ff-openSans);
}

h3 {
  font-size: var(--fs-h3);
  font-family: var(--ff-openSans);
}

h4 {
  font-size: var(--fs-h4);
  font-family: var(--ff-openSans);
  margin-top: 0;
  margin-bottom: 3em;
}

h5 {
  font-size: var(--fs-h5);
  font-family: var(--ff-openSans);
}

p {
  word-spacing: 1.4px;
  font-family: var(--ff-openSans);
}

.btn-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-bg1 {
  font-size: var(--fs-h4);
  font-weight: 700;
  text-decoration: none;
  padding: 1em 2.25em;
  color: var(--clr-dark);
  background-color: var(--clr-accent);
  border-radius: 10px;
  min-width: 20em;
  margin-right: 1.5em;
  transition: all 0.3s;
}

.btn-bg1:hover,
.btn-bg1:focus {
  background-color: var(--clr-light);
}

@media (max-width: 700px) {
  .btn-container {
    flex-direction: column;
  }

  .btn-bg1 {
    margin-right: 0;
    margin-bottom: 1.5em;
  }
}

.btn-bg2 {
  font-size: var(--fs-h4);
  font-weight: 700;
  text-decoration: none;
  padding: 1.207em 2.25em;
  color: var(--clr-light);
  background-color: #000;
  border-radius: 10px;
  min-width: 20em;
  transition: all 0.3s;
}

.lg-container {
  width: 90%;
  margin: 0 auto;
}

.md-container {
  width: 80%;
  margin: 0 auto;
}

.sm-container {
  width: 65%;
  margin: 0 auto;
}

.lg_section-spacing {
  margin-bottom: 25rem;
}

.section-spacing {
  margin-bottom: 20rem;
}

.md_section-spacing {
  margin-bottom: 10rem;
}

.smMD_section-spacing {
  margin-bottom: 7.5rem;
}

.sm_section-spacing {
  margin-bottom: 5rem;
}

.smLogo {
  width: var(--fs-smLogo);
}

.logo {
  width: var(--fs-logo);
}

.discord {
  color: var(--clr-dark);
  font-size: 2.5rem;
  margin-right: 0.3em;
}

@media (max-width: 1600px) {
  .discord {
    font-size: 1.76rem;
  }
}

@media (max-width: 425px) {
  .discord {
    font-size: inherit;
  }
}

.discord-a {
  display: flex;
  justify-content: center;
  align-items: center;
}
